<template>
  <div>
    <!-- <v-app-bar app src="https://picsum.photos/1920/1080?random"> </v-app-bar> -->

    <v-container style="max-width: 900px">
      <v-row justify="center" class="d-flex align-content-start flex-wrap">
        <v-col v-for="(item, i) in list" :key="i" cols="auto">
          <v-card :elevation="4" height="150" width="426"  @click="sec(item)">
            <v-img
              src="https://picsum.photos/273/120?random"
              height="80px"
            ></v-img>
            <v-card-title>{{ item.label }}</v-card-title>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
export default {
  data: () => ({
    list: [ 
      { label:"Randevu Al", page:"randevual"  },
      { label:"Randevu Al Popup", page:"randevualpopup"  },
      { label:"Vekalet ile Randevu Al", page:"rrr" },],
  }),
  methods: {
    sec(item) {
      this.$router.push({ path: item.page });
    },
  },
  mounted() {
    var token = sessionStorage.getItem("token");
    if (!token) {
      this.$router.push({
        name: "kimlikdogrula",
        params: { returnpage: "randevusec" },
      });
    }
  },
};
</script>