import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import AnaMenu from '../views/AnaMenu.vue';
import RandevuSec from '../views/RandevuSec.vue';
import KimlikDogrula from '../views/KimlikDogrula.vue';
import MudurlukSec from '../views/MudurlukSec.vue';
import Randevularim from '../views/Randevularim.vue';
import Basvurularim from '../views/Basvurularim.vue';
import PasaportTalepFormlari from '../views/PasaportTalepFormlari.vue';
import BasvuruUcretleri from '../views/BasvuruUcretleri.vue';
import SikSorulanSorular from '../views/SikSorulanSorular.vue';
import RandevuAl from '../views/RandevuAl.vue';
import RandevuAlpopup from '../views/RandevuAlpopup.vue';
Vue.use(VueRouter)

const routes = [
  // {
  //   path: '/',
  //   name: 'Home',
  //   component: Home,
  // },
  {
    path: '/',
    name: 'menu',
    component: AnaMenu,
  },
  {
    path: '/randevusec',
    name: 'randevusec',
    component: RandevuSec,
  },
  {
    path: '/randevularim',
    name: 'randevularim',
    component: Randevularim,
  },
  {
    path: '/basvurularim',
    name: 'basvurularim',
    component: Basvurularim,
  },
  {
    path: '/kimlikdogrula/:returnpage',
    name: 'kimlikdogrula',
    component: KimlikDogrula,
   
    props: route => ({ returnpage: route.query.returnpage })
  },
  {
    path: '/mudurluksec',
    name: 'mudurluksec',
    component: MudurlukSec,
  },
  {
    path: '/pasaporttalepformlari',
    name: 'pasaporttalepformlari',
    component: PasaportTalepFormlari,
  },
  {
    path: '/basvuruucretleri',
    name: 'basvuruucretleri',
    component: BasvuruUcretleri,
  },
  {
    path: '/siksorulansorular',
    name: 'siksorulansorular',
    component: SikSorulanSorular,
  },
  {
    path: '/randevual',
    name: 'randevual',
    component: RandevuAl,
  },
  {
    path: '/randevualpopup',
    name: 'randevualpopup',
    component: RandevuAlpopup,
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/About.vue')
    }
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
